<template>
  <v-container
    fluid
    grid-list-md>
    <v-form>
      <v-layout
        row
        wrap>
        <core-progress-modal :show="loading" />
        <template v-if="!loading">
          <v-flex md12>
            <v-card class="elevation-0">
              <v-card-title>
                <h3 class="headline mb-0">Reembolsar Repasse</h3>
              </v-card-title>
              <v-card-text>
                <v-layout
                  row
                  wrap>
                  <v-flex md9>
                    <core-filtro-rede-empresa-loja
                      v-model="form"
                      :show-todas="true"
                      :obrigatorio="true"
                    />
                  </v-flex>
                  <v-flex md3>
                    <v-text-field
                      v-model="form.vencimentoAte"
                      type="date"
                      label="Vencimento at√©"
                    />
                  </v-flex>
                </v-layout>
                <v-layout class="justify-end">
                  <v-btn
                    :disabled="disableBtn"
                    :loading="loadingBtn"
                    color="primary"
                    @click="gerar">Gerar</v-btn>
                </v-layout>
              </v-card-text>
            </v-card>
            <v-card
              class="mt-3"
            >
              <v-data-table
                v-model="selected"
                :headers="headers"
                :items="items"
                class="elevation-1 arrow-pagintion"
                select-all
                item-key="lojaId"
                hide-actions
              >
                <template
                  slot="headers"
                  scope="props"
                >
                  <tr>
                    <th
                      style="width:3%"
                    >
                      <v-checkbox
                        :input-value="props.all"
                        :indeterminate="props.indeterminate"
                        hide-details
                        @click.native="toggleAll"
                      />
                    </th>
                    <th
                      v-for="header in props.headers"
                      :key="header.text"
                      :class="header.class"
                      :align="header.align"
                    >
                      {{ header.text }}
                    </th>
                  </tr>
                </template>
                <template
                  slot="items"
                  scope="props"
                  ma-5>
                  <tr
                    :active="props.selected">
                    <td>
                      <v-checkbox
                        :input-value="props.selected"
                        primary
                        hide-details
                        @click="props.selected = !props.selected"
                      />
                    </td>
                    <td>
                      {{ props.item.redeNome }}
                    </td>
                    <td>
                      {{ props.item.lojaNome }}
                    </td>
                    <td
                      v-negative="props.item.vlTotalCompras"
                      class="text-xs-right"
                    >
                      {{ formatValorMonetario(props.item.vlTotalCompras) }}
                    </td>
                    <td
                      v-negative="props.item.vlTotalComissaoCompras"
                      class="text-xs-right"
                    >
                      {{ formatValorMonetario(props.item.vlTotalComissaoCompras) }}
                    </td>
                    <td
                      v-negative="props.item.vlTotalCreditoAdmin"
                      class="text-xs-right"
                    >
                      {{ formatValorMonetario(props.item.vlTotalCreditoAdmin) }}
                    </td>
                    <td
                      v-negative="props.item.vlTotalPagamentosLoja"
                      class="text-xs-right"
                    >
                      {{ formatValorMonetario(props.item.vlTotalPagamentosLoja) }}
                    </td>
                    <td
                      v-negative="props.item.vlTotalCreditoLojista"
                      class="text-xs-right"
                    >
                      {{ formatValorMonetario(props.item.vlTotalCreditoLojista) }}
                    </td>
                    <td
                      v-negative="props.item.vlTotalPagar"
                      class="text-xs-right"
                    >
                      {{ formatValorMonetario(props.item.vlTotalPagar) }}
                    </td>
                  </tr>
                </template>
              </v-data-table>
              <v-card-actions>
                <v-spacer />
                <v-btn
                  :disabled="selected.length === 0"
                  :loading="loadingBtn"
                  color="primary"
                  @click="openDialogBoleto"
                >Efetuar Reembolso</v-btn>
              </v-card-actions>
            </v-card>
          </v-flex>
        </template>
      </v-layout>
    </v-form>
    <template>
      <v-layout
        row
        justify-center>
        <v-dialog
          v-model="dialogBoleto"
          max-width="490">
          <v-card>
            <v-card-title class="headline">Confirma Emiss√£o de Boleto</v-card-title>
            <v-alert
              :value="true"
              type="warning"
            >
              <span>Ser√° enviado um email para a empresa e ao entrar no sistema Pr√°tico receber√° uma notifica√ß√£o com o boleto para pagar.</span>
            </v-alert>
            <v-card-text>
              <p>
                Confirma a emiss√£o de boleto para cada empresa selecionada,
                com valor correspondente ao saldo acumulado a pagar
                e com vencimento <span class="title"> {{ form.vencimentoAte | moment("DD/MM/YYYY") }} </span>?
              </p>
            </v-card-text>

            <v-card-actions>
              <v-btn
                outline
                style="color: black !important"
                @click="dialogBoleto = false">Cancelar</v-btn>

              <v-spacer/>

              <v-btn
                color="primary"
                @click="gerarBoleto()">Confirmar</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-layout>
    </template>
  </v-container>
</template>

<script>
import { ReembolsoBusiness } from '../../../business'
import numberUtils from '../../../utils/numberUtils'
import { TODAS_ID } from '../../../constants'
export default {
  data () {
    return {
      selected: [],
      dialogBoleto: false,
      form: {
        redeId: TODAS_ID,
        empresaId: TODAS_ID,
        lojaId: TODAS_ID,
        redeNome: null,
        empresaNome: null,
        vencimentoAte: null,
        lojasSelecionadas: []
      },
      items: [],
      headers: [
        {
          sortable: false,
          text: 'Rede',
          value: 'redeNome',
          align: 'left',
          class: 'grey lighten-1'
        },
        {
          sortable: false,
          text: 'Empresa (Loja)',
          align: 'left',
          value: 'lojaNome',
          class: 'grey lighten-1'
        },
        {
          sortable: false,
          text: 'Compras',
          value: 'compras',
          align: 'right',
          class: 'grey lighten-1'
        },
        {
          sortable: false,
          text: 'Comiss√£o compras',
          value: 'comissaoCompras',
          align: 'right',
          class: 'grey lighten-1'
        },
        {
          sortable: false,
          text: 'Cr√©ditos admin',
          value: 'creditosAdmin',
          align: 'right',
          class: 'grey lighten-1'
        },
        {
          sortable: false,
          text: 'Pagtos em loja',
          value: 'pagtosLoja',
          align: 'right',
          class: 'grey lighten-1'
        },
        {
          sortable: false,
          text: 'Cr√©ditos lojista',
          value: 'creditosLojista',
          align: 'right',
          class: 'grey lighten-1'
        },
        {
          sortable: false,
          text: 'Saldo acumulado a pagar',
          value: 'vlTotalPagar',
          align: 'right',
          class: 'grey lighten-1'
        }
        /*,
        {
          sortable: false,
          class: 'grey lighten-1'
        } */
      ],
      loading: false,
      loadingBtn: false
    }
  },
  computed: {
    disableBtn () {
      return !(this.form.redeId && this.form.empresaId && this.form.vencimentoAte)
    }
  },
  methods: {
    toggleAll () {
      if (this.selected.length) this.selected = []
      else this.selected = this.items.slice()
    },
    gerar () {
      this.loadingBtn = this.loading = true
      ReembolsoBusiness.calculaReembolsoRepasse(this.form)
        .then(response => {
          this.items = response.data
          this.toggleAll()
          if (this.items.length === 0) {
            this.$root.showAlerta('N√£o foram encontrados registros')
          }
        })
        .catch(() => this.$root.showErro('Erro ao tentar gerar o Reembolso Repasse, tentar mais tarde!'))
        .finally(() => {
          this.loadingBtn = this.loading = false
        })
    },
    openDialogBoleto () {
      this.dialogBoleto = true
    },
    gerarBoleto () {
      this.form.lojasSelecionadas = this.selected.map(item => item.lojaId)
      this.loading = true
      ReembolsoBusiness.efetuarReembolso(this.form)
        .then(response => {
          this.$root.showSucesso(response.data)
          this.dialogBoleto = false
        }).catch(erro => {
          this.$root.showErro(erro.data)
        }).finally(() => {
          this.loading = false
        })
    },
    formatValorMonetario (valor) {
      return numberUtils.formatValorMonetario(valor)
    }
  }
}
</script>
